/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { flexRowCSS } from "Common/css"
import { useCartDispatch } from "../_state/hooks"
import { useShopState } from "../../shop/_state/hooks"

function CartTerms() {
  const { useState } = wp.element
  const cartDispatch = useCartDispatch()
  const shopState = useShopState()
  const [isChecked, setIsChecked] = useState(() => false)

  function termsLabel() {
    return {
      __html: shopwp.general.cartTermsContent,
    }
  }

  function onChange(e) {
    if (shopState.isCartUpdating) {
      return
    }

    setIsChecked(!isChecked)
    cartDispatch({ type: "SET_TERMS_ACCEPTED", payload: !isChecked })
  }

  var containerCSS = css`
    margin: 0.5em 0 1em 0;
    padding: 0;
  `

  var labelCSS = css`
    padding-left: 7px;
    width: 100%;
    text-transform: initial;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin: 0;
    font-weight: normal;
    font-size: 15px;
    color: #121212;
    cursor: ${shopState.isCartUpdating ? "not-allowed" : "pointer"};

    &:empty {
      display: none;
    }
  `

  const termsWrapperCSS = css`
    align-items: center;

    .wps-input {
      margin: 0;
      width: auto;

      &:hover {
        cursor: pointer;
      }
    }
  `

  const termsInputCSS = css`
    width: 13px;
    height: 13px;
    border-radius: none;
    cursor: ${shopState.isCartUpdating ? "not-allowed" : "pointer"};

    &:disabled {
      &:hover {
        cursor: not-allowed;
      }
    }
  `

  return (
    <section className="wps-cart-terms" css={containerCSS}>
      <div className="wps-input-row" css={[flexRowCSS, termsWrapperCSS]}>
        <input
          onChange={onChange}
          id="wps-input-terms"
          defaultChecked={isChecked}
          type="checkbox"
          className="wps-input wps-input-checkbox"
          css={termsInputCSS}
        />
        <label
          dangerouslySetInnerHTML={termsLabel()}
          htmlFor="wps-input-terms"
          className="wps-input-label wps-input-terms"
          css={labelCSS}
        />
      </div>
    </section>
  )
}

export default CartTerms
